.popup {
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba($color-black, .8);
    z-index: 5000;

    opacity: 0;
    visibility: hidden;
    transition: all .3s;

    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba($color-black, .3);


    &__content {
        @include absCenter;
        width: 75%;
        background-color: $color-white;
        box-shadow: 0 2rem 4rem rgba($color-black, .2);
        border-radius: 3px;
        display: table;
        overflow: hidden;

        opacity: 0;
        transform: translate(-50%, -50%) scale(.25);
        transition: all .5s .2s;
        
        @include respond(tab-land) {
            width: 85%;
        }

        @include respond(tab-port) {
            height: 75%;
            width: 85%;
        }

        @include respond(tab-phone) {
            height: 50%;
            width: 85%;
        }
    }

    &__left {
        width: 33.333333%;
        height: 100%;
        display: table-cell;

        @include respond(tab-port) {
            width: 100%;
            height: 33.333333%;
            display: block;
            width: 85%;
        }

        @include respond(phone) {
            height: 0;
            visibility: hidden;
        }
    }

    &__right {
        width: 66.6666667%;
        display: table-cell;
        vertical-align: middle;
        padding: 3rem 5rem;

        @include respond(tab-land) {
            height: 100%;
            padding: 1rem 3rem;
        }

        @include respond(tab-port) {
            width: 100%;
            height: 66.6666667%;
            display: block;
            text-align: center;
        }

        @include respond(phone) {
            width: 100%;
            height: 100%;
            display: block;
            text-align: center;
            padding-top: 7rem;
        }
    }

    &__img {
        display: block;
        width: 100%;

        @include respond(tab-port) {
            display: inline-block;
            width: 50%;
            transform: scale(1.5);
            padding-left: 9rem;
            padding-right: 1rem;
            margin-top: -4rem;
            overflow: hidden;
        }
    }

    &__text {
        font-size: 1.4rem;
        margin-bottom: 4rem;

        -moz-column-count: 2;     //-moz- for the moz browser
        -moz-column-gap: 4rem;
        -moz-column-rule: 1px solid $color-gray-light-2;

        column-count: 2;
        column-gap: 4rem;
        column-rule: 1px solid $color-gray-light-2;

        -moz-hyphens: auto;
        -ms-hyphens: auto;
        -webkit-hyphens: auto;
        hyphens: auto;

        @include respond(tab-land) {
            column-count: 1;
            margin-bottom: 2rem;
        }

        @include respond(tab-port) {
            margin-bottom: 3rem;
        }
    }

    
    // opening and closing functionality

    //open states
    &:target {
        opacity: 1;
        visibility: visible;
    }

    &:target &__content {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }

    &__close {
        &,
        &:link,
        &:visited {
            color: $color-gray-dark;
            position: absolute;
            top: 2.5rem;
            right: 2.5rem;
            font-size: 3rem;
            text-decoration: none;
            display: inline-block;
            transition: all .2s;
            line-height: 1;
        }

        &:hover {
            color: $color-primary;
        }
    }
}